@mixin shadow {
    box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;    
}

.main{
    height: 98.5vh;
    width: 100%;
    max-width: 100%;
    padding: 0 .5rem;
    display: grid;
    grid-template-areas: 
    "a a a a a"
    "b c c c d";
    gap: .5rem;
    grid-template-rows: 10% 90%;

    &_header{
        grid-area: a;
        background-color: white;
        display: flex;
        flex-direction: row;
        align-items: center;
        @include shadow();
        padding: 0 2rem;

        & > div{
            font-weight: 500;
            font-size: 1.75rem;
            &:nth-child(1){
                flex: .33;
            }
            &:nth-child(2){
                flex: .33;
                display: flex;
                justify-content: center;
            }
            &:nth-child(3){
                flex: .33;
                display: flex;
                justify-content: flex-end;
                flex-direction: row;

                & > div{
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    
                    &:nth-child(1){
                        margin-right: 1rem;
                    }
                }
            }
        }
    }

    &_side{
        width: clamp(20vw, 20vw, 20vw);
        grid-area: b;
        @include shadow();
        padding: 0 .5rem;
        background-color: #efd;
    }

    &_area{
        @include shadow();
        width: clamp(58.25vw, 58.25vw, 58.25vw);
        grid-area: c;
        background-color: #fee;
    }

    &_right{
        background-color: #efd;
        @include shadow();
        padding: 0 .5rem;
        width: clamp(20vw, 20vw, 20vw);
        grid-area: d;
    }
}