@import "../../components/Executive/Login/Login.module.scss";

.card{
    max-width: 58vw;
    width: 100%;
    position: relative;
    height: 80vh;
    padding-top: 0;
    margin-top: .4rem;
    display: flex;
    justify-content: flex-start;

    &_heading{
        cursor: pointer;
        margin: 0;
        width: calc(100% + 2rem);
        background-color: #ddd;
        padding: 1rem;
        font-size: 2rem;
        border-bottom: .2rem solid #ccc;
        font-weight: 500;
     
        &::after{
            display: none;
        }
    }

    &_end{
        right: 0;
        top: 0;
        position: absolute;
        width: fit-content;
        padding: 0 3rem;
        background: #f04;
        transform: translate(105%, 0);
    
        bottom: 0;
        right: unset;
        left: 0;
        z-index: 1;
        transform: translate(0, 110%);
        top: unset;
        width: 100%;
    }

    &_scroll{
        height: 82%;
        width: 100%;
        display: flex;
        flex-direction: column;
 
        &_container{
            width: 100%;
            display: flex;
            justify-content: flex-start;
            padding-bottom: 1.5rem;
            padding-right: 1rem;
        }

        &_item{
            display: flex;
            max-width: 80%;
            margin-bottom: .25rem;
            background-color: #fef;
            width: fit-content;
            height: fit-content;
            padding: .5rem .75rem;
            border-radius: .4rem;
            flex-direction: column;

            &_text{
                font-size: 1.5rem;
                font-weight: 300;
            }

            &_time{
                font-size: 1rem;
                color: #777;
            }
        }
    }

    &_input{
        position: absolute;
        bottom: 0;
        left: 0;
        height: fit-content;
        width: 100%;
        & > input{
            border-radius: 0;
            height: fit-content;
            font-weight: 300;
            // overflow: hidden;
            overflow-y: scroll;
            padding-right: 30%;
            border: .1rem solid rgba(#113, .6);
        }

        &_button{
            position: absolute;
            width: 25%;
            top: 50%;
            transform: translate(0, -52%);
            right: .5rem;
            height: calc(100% - 1rem);
        
            &_key{
                margin-left: 1rem;
                font-size: 1.2rem;
                color: #eee;
                margin-bottom: .25rem;
                background-color: #777;
                padding: .5rem 1rem;
                border-radius: .5rem;
                // font-family: "Raleway", sans-serif;
                // font-weight: 500;
                box-shadow: rgba(0, 0, 0, 0.45) 3px 3px .1px;
                // text-transform: capitalize !important;
            }
        }
    }
}