@import "../../../sass/animations";

.main{
    position: relative;
    padding: 1rem;
    height: 100%;
    background-color: red;
    

    &_heading{
        font-weight: 600;
        // margin-left: 1rem;
        font-size: 2rem;
        font-family: "Raleway", sans-serif;

        &::after{
            height: .2rem;
        }
    }

    &_flex{
        width: 100%;
        // max-width: 100%;
        width: 40vw;
        min-width: 40vw;

        &_warning{
            transform: none;
        }
    }

    & > div > p{
        margin-block-end: 0;
        margin-block-start: 0;
        margin-bottom: .4rem;
    }

    &_item{
        font-size: 2rem;
        width: 100%;
        height: fit-content;
        min-height: 6vh;
        cursor: pointer;
        display: flex;
        border: .1rem solid #cdf;
        flex-direction: row;
        align-items: center;
        padding: .25rem 1rem;
        border-radius: .25rem;
        background: #eef;
        margin-bottom: .5rem;

        &_button{
            flex: .2;
            height: 2.5rem !important;
            // padding: 0 3rem;
            font-size: 1.1rem !important;
            // font-weight: 300 !important;
        }

        &_text{
            font-family: "Raleway", sans-serif;
            flex: .8;
            font-size: 1.4rem;
            display: flex;
            flex-direction: column;

            &_title{
                font-size: 1.8rem;
                margin-bottom: .5rem;
            }

            &_time{
                font-size: 1.25rem;
                color: #555;
                font-family: "PT Sans", sans-serif;
            }
        }
    }
}

.ended{
    color: #888;
}

.beat{
    animation: heartBeat .5s ease-in forwards;
}

.pulse{
    animation: pulse 3s ease-in-out forwards infinite;
    animation-delay: .5s;
}