.main{
    display: flex;
    width: 100%;
    flex: 1;
    padding: 1rem;
    border-bottom: .2rem solid #bbb;
    justify-content: flex-start;
    flex-direction: column;
    font-family: "Raleway", sans-serif;

    &:last-child{
        border-bottom: none;
    }

    &_key{
        color: #555;
        font-weight: 600;
        font-size: 1.4rem;
    }

    &_value{
        font-family: "PT Sans", sans-serif;
        font-size: 2rem;
        font-weight: 700;
    }
}

.board{
    height: fit-content;
    width: 100%;
    z-index: 1;
    border: .2rem solid #ddd;
    padding: 2rem 1rem;
    border-radius: 1rem;
    background: #eef;

    &_heading{
        margin-top: 0;
    }

    &_row{
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;

        &_icon{
            cursor: pointer;
            font-size: 3rem;
        }
    }
}