@import "../../../sass/mixins";
@import "../../../sass/variables";

.main{
    height: 100vh;
    display: flex;
    justify-content: center;

    &_card{
        height: 80vh;
        width: 30%;
        flex-direction: column;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: auto auto;

        @include respond(tab-port){
            width: 100%;
            height: 100vh;
        }
    }
}
