.main{
    display: flex;
    flex-direction: row;
    min-height: 100vh;
    width: 100%;

    &_left{
        width: 40%;
        height: 100%;
        box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
        background-color: #dfd;
        min-height: 100vh;

        &_scroll{
            height: 100vh;
            min-height: 100vh;
            max-height: 100vh;

            & > div{
                max-height: 40vh;

                & > div{
                    height: 100%;
                    max-height: 100%;
                
                    & > div{
                        min-height: 45vh;
                        max-height: 45vh;
                    }
                }
                &:nth-child(1){
                    margin-bottom: 2rem;
                }
            }
        }
    }

    &_right{
        position: relative;
        padding: 0 .5rem;

        &_warning{
            transform: none;
            z-index: 1;
        }
    }
}